import React from 'react'
import './Goods.css'
import {NavBar,Icon} from 'antd-mobile'
import {withRouter} from 'react-router-dom'


class Goods extends React.Component {
    
    constructor(props, context) {
        super(props, context)
        console.info(props)
        this.userList = props.location.state.data
        console.log(this.userList)
      }
    render() {
        var userList = {};
        // const { pathname } = this.props.location;
        return (
            <div>
                <div>
                    <NavBar
                        mode="light"
                        icon={<Icon type="left" />}
                        onLeftClick={() => {
                            this.props.history.push('/home');
                        }}
                    >
                        商品详情页
                    </NavBar>
                </div>

                <div class='img-detail'>
                    <img style={{width:'100%'}} src={`${this.userList.imgs}`} />
                </div>
                <div class="real-price">
                    <span class="ui-yen ">
                        <i class="price-symbol">¥ </i>
                    <span class="price">{this.userList.dataPrice}</span><br />
                    <span class='information'>{this.userList.dataInf}</span><br />
                    <span class='num'>{this.userList.dataNum}人已购买</span>
                    </span>
                </div>

                <div class="bottom-bar">
                    <div class="btn-shop">
                        <span class="iconfont icon-dianpu" style={{padding:'20%'}}></span>
                        <span class="btn-text">店铺</span>
                    </div>
                    <div class="btn-wangwang">
                        <span class="iconfont icon-kefu" style={{padding:'20%'}}></span>
                        <span class="btn-text">客服</span>
                    </div>
                    <div class="btn-fav">
                        <span class="iconfont icon-shoucang" style={{padding:'20%'}}></span>
                        <span class="btn-text">收藏</span>
                    </div>
                    <div class="bottom-bar-btn cart btn-pos-left ">
                        <span class="btn-title">加入购物车</span>
                    </div>
                    <div class="bottom-bar-btn buy btn-pos-right ">
                        <span class="btn-title">立即购买</span>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Goods)
