import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-ico-'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuchekong'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwucheman'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan2'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan2'></i>
                        }
                        title="订单列表"
                        key="list"
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')
                        } }
                        data-seed="logId2"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-ziyuan'></i>
                         }
                        selectedIcon={ 
                            <i className='iconfont icon-ziyuan'></i>
                        }
                        title="我的淘宝"
                        key="my"
            
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId3"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gengduo'></i>
                         }
                        selectedIcon={ 
                            <i className='iconfont icon-gengduomore10'></i>
                        }
                        title="更多"
                        key="more"
            
                        selected={ pathname === '/more' }
                        onPress={ () => {
                            this.props.history.push('/more')
                        } }
                        data-seed="logId4"
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);